/********************************** 
START - SVG Spaceman 
***********************************/

svg {
  position: absolute;

  height: 2.5rem;
  top: 0rem;
  left: 0rem;
  fill: white;
  animation: moonMan 50s linear infinite;
}

@keyframes moonMan {
  0% {
    top: -3.125rem;
    left: -3.125rem;
    transform: rotate(0deg);
  }
  16% {
    left: 50rem;
    top: 31.25rem;
  }
  32% {
    top: 37.5rem;
    left: -3.125rem;
  }
  48% {
    left: 112.5rem;
    top: 18.75rem;
  }
  64% {
    top: -3.125rem;
    left: -9.375rem;
    
  }
  80% {
    top: -9.375rem;
    left: -9.375rem;
    
  }
  100% {
    top: -3.125rem;
    left: -3.125rem;
    transform: rotate(360deg);
  }
}

