<template>
<div id="nodemailer">

      <div id="nodemailer-container" class="row">
            <div class="col">
                
                <p class="address"><b>Monday - Saturday:</b></p>
                <p class="address">12:00 - 19:00</p>
                <p class="address"><b>Sunday:</b></p>
                <p class="address">12:00 - 18:00</p>
                <p class="callUs"><b>Call us</b></p>
                <p class="address">+45 53 66 85 88</p> 
            </div>
            <div class="col">
                <img  src="../assets/contactImg/wallpaper.jpg" alt="">
            </div>  
      </div>

      <div id="formText" class="row">
          <form class="col" @submit.prevent="handleSubmit">
            <input type="email" placeholder="Your email" required v-model="email" />
          
            <input type="text" placeholder="Your Name" required v-model="name" />
            
            <textarea id="messageArea" placeholder="Your message to us" maxlength="500" v-model="message" required></textarea>
            <p id="max500" v-bind:class="lettersOver">500 / {{ lettersRemaining }}</p>
          
            <button id="btnForm">Submit</button>
          </form>
          <div class="col" id="contactTextRight">
              <h1>Contact Us</h1>
              <p>Send us your feedbacks and inquiries and don’t forget you can also message Tea:licious Café on your favorite social media platforms!</p>
              <p class="address"><b>Email:</b></p>
              <p class="address">info@bubble-t.com</p>

          </div>
      </div>


</div>
</template>

<script>
export default {
  name: "Nodemailer",
  data() {
    return {
      email: "",
      name: "",
      message: "",
      maxCharacters: 0

    };
  },
  computed: {
    //count the letters 
    lettersRemaining: function () {
      return this.maxCharacters + this.message.length;
    },
    //Is this over 500 letters ?
    lettersOver: function () {
      return (this.message.length > 500) ? "lettersOver" : false ;
    }

  },
  methods: {
    handleSubmit() {
        
        const email = this.email;
        const name = this.name;
        const message = this.message;
        

        const data = {
          email,
          name,
          message
        };

        const options = {
          method: 'POST',
          headers:{
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(data)
        };

        fetch('http://localhost:3000/nodemailer', options)

          .then((response) => response.json())
          .then((responseJSON) => {
              //Send it !!!
              if (responseJSON.status == "OK") {
                console.log(responseJSON);
                console.log("Email has been send in the front!!!");

              }
              
           
          }).catch(error => {
              // handle the error
               console.log("error error error error");
          });



        //reset the form
        // this.email = "";
        // this.name = "";
        // this.message = "";

    },
  },
};
</script>

<style>



div#nodemailer {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-left: 14%;
    padding-right: 14%;
}


form {
  text-align: left;
  max-width: 43.75rem;
}

#max500{
  margin: 0rem;
  text-align: right;
  max-width: 36.25rem;
}


/* Style inputs with type="text", select elements and textareas */
input[type="text"],
input[type="email"],
textarea,
select {
  width: 100%; /* Full width */
  padding: 0.75rem; /* Some padding */
  border: 0.063rem solid #ccc; /* Gray border */
  border-radius: 0.25rem; /* Rounded borders */
  box-sizing: border-box; /* Make sure that padding and width stays in place */
  margin-top: 0.375rem; /* Add a top margin */
  margin-bottom:1rem; /* Bottom margin */
  resize: vertical; /* Allow the user to vertically resize the textarea (not horizontally) */
}

/* Style the submit button with a specific background color etc */
#btnForm {
  background-color: #04aa6d;
  color: white;
  padding: 0.75rem 1.25rem;
  border: none;
  border-radius: 1.25rem;
  cursor: pointer;
}

/* When moving the mouse over the submit button, add a darker green color */
#btnForm:hover {
  background-color: #45a049;
}



#messageArea{
  margin-bottom: 0;
  height: 11.25rem;
}

#contactTextRight h1{
    font-size: 2rem;
    margin-top: 0;
}

div#nodemailer-container {
    margin-top: 9.875rem;
    align-items: center;
}
div#nodemailer-container img {
    width: 100%;
    border-radius: 1.563rem;
}










</style>
